<template>
    <div>
        <span class="btn" @click="alert"></span>
        <div v-show="show"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        alert() {
            this.$alert({
                title: "",
                parseHtml: true,
                okText: "",
                content: "<div class='red'></div>"
            })
                .then(() => {
                    this.show = !this.show;
                })
        }
    }
};
</script>