let confirm= `
<template>
    <div>
        <span class="btn" @click="confirm"></span>
        <div v-show="show"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        confirm() {
            this.$confirm("")
                .then(() => {
                    this.show = true;
                })
                .catch(e => {
                    this.show = false;
                });
        }
    }
};
</script>
`;

let alert1 = `
<template>
    <div>
        <span class="btn" @click="alert"></span>
        <div v-show="show"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        alert() {
            this.$alert({
                title: "",
                parseHtml: true,
                okText: "",
                content: "<div class='red'></div>"
            })
                .then(() => {
                    this.show = !this.show;
                })
        }
    }
};
</script>
`;

var state = {
    confirm: confirm,
    alert: alert1
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}