<template>
    <div>
        <span class="btn" @click="confirm"></span>
        <div v-show="show"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            show: false
        };
    },
    methods: {
        confirm() {
            this.$confirm("")
                .then(() => {
                    this.show = true;
                })
                .catch(e => {
                    this.show = false;
                });
        }
    }
};
</script>