<template>
    <div>
        <H3></H3>
        <v-group></v-group>
        <v-group>
            
            <span class="code">Vue.prototype</span>
            <span class="code">$valid</span>
        </v-group>
        <v-code :code="dataCheck" :isShow="true"></v-code>
        <v-group>
            all
            <div>args</div>
        </v-group>

        <h5></h5>
        <v-group>
            Vue
            <span>$checkData</span>
            <div>falseerror</div>
        </v-group>
        <h6></h6>
        <v-code :code="check1" :isShow="false">
            <data-check></data-check>
        </v-code>
        <v-group>dataKey.error </v-group>
        <h5></h5>
        <v-group>
            <div>Vue <span class="code">$checkAll</span></div>
            <div></div>
            <div>Boolean true false</div>
            <div>key</div>
        </v-group>
        <v-code :code="groupCheck" :isShow="false">
            <example-check></example-check>
        </v-code>
        
        <!-- <v-code>
            <div v-html="code1"></div>
        </v-code> -->
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import GroupCheck from "../example/dataCheck/groupCheck";
import DataCheck from "../example/dataCheck/checkData";

export default {
    components: {
        "example-check": GroupCheck,
        "data-check": DataCheck
    },
    computed: {
        ...mapGetters("dataCheck", ["dataCheck", "check1", "groupCheck"])
    },
    data() {
        return {
           
            formData: {
                sendInterval: {
                    maxlength: 5,
                    valid: {
                        type: "num",
                        args: [5, 32768]
                    }
                },
                ttlNum: {
                    maxlength: 2,
                    valid: {
                        type: "num",
                        args: [2, 10]
                    }
                },
                sendDelayTime: {
                    maxlength: 4,
                    valid: {
                        type: "num",
                        args: [1, 8192]
                    }
                },
                intDelayTime: {
                    maxlength: 2,
                    valid: {
                        type: "num",
                        args: [1, 10]
                    }
                }
            }
        };
    },
    methods: {
        submit() {
            let checkSuccess = this.$checkAll(this.formData);

            if(!checkSuccess) {
                this.$message("");
                return;
            }
            this.$message("OK");
            //your code
        }
    }
};
</script>