let checkbox1 = `
<template>
    <div>
        <v-checkbox :data-key="checkbox"></v-checkbox>
        <div>{{checkbox.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            checkbox: {
                values: [true, false],
                val: false,
                sortArray: [{
                    value: true,
                    title: ""
                }],
                changeCallBack(value) {
                    console.log("checkbox value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickCheckbox
                }
            }
        };
    },
    methods: {
        clickCheckbox() {
            console.log("", this.checkbox.val);
        }
    }
}
</script>
`;
