let radio = `
<template>
    <div>
        <v-radio :data-key="radio"></v-radio>
        <div>{{radio.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            radio: {
                val: "1",
                sortArray: [{
                    value: "1",
                    title: ""
                },
                {
                    value: "0",
                    title: ""
                }],
                changeCallBack(value) {
                    console.log("radio value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickRadio
                }
            }
        };
    },
    methods: {
        clickRadio() {
            console.log("", this.radio.val);
        }
    }
}
</script>
`;

let disabled = `
<template>
    <div>
        <v-radio :data-key="radio"></v-radio>
        <div>{{radio.val}}</div>
    </div>
</template>
<script>
export default {
    data() {
        return  {
            radio: {
                val: "1",
                sortArray: [{
                    value: "1",
                    title: ""
                },
                {
                    value: "0",
                    title: ""
                },
                {
                    value: "-1",
                    title: "",
                    disabled: true
                }],
                changeCallBack(value) {
                    console.log("radio value ",value);
                },
                immediate: false,
                events: {
                    click: this.clickRadio
                }
            }
        };
    },
    methods: {
        clickRadio() {
            console.log("", this.radio.val);
        }
    }
}
</script>
`;
var state = {
    radio: radio,
    disabled: disabled
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}