<template>
    <v-table ref="table" :tableOptions="tableData" @on-custom-comp="customCompFunc"></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                columns: [
                    {
                        title: "",
                        field: "index"
                    },
                    {
                        title: "",
                        field: "name"
                    },
                    {
                        title: "",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "" : "";
                        }
                    },
                    {
                        title: "",
                        field: "address"
                    },
                    {
                        title: "",
                        action: ["edit", "delete"],
                        componentName: 'table-operation'
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 2,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 3,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 4,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 5,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    }
                ]
            }
        };
    },
    mounted() {
        //this.tableData.originData = ;
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //
                case "edit":
                    this.editRule(options);
                    break;
                //
                case "delete":
                    this.delRule(options);
                    break;
            }
        },
        editRule(options) {
            this.$message("");
        },
        delRule(options) {
            this.$message("");
        }
    }
};
</script>