let customTable = `
<template>
    <v-table
        ref="table"
        :tableOptions="tableData"
        @on-custom-comp="customCompFunc"
    ></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                selectBox: true,
                columns: [
                    {
                        title: "",
                        field: "index"
                    },
                    {
                        title: "",
                        field: "name"
                    },
                    {
                        title: "",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "" : "";
                        }
                    },
                    {
                        title: "",
                        field: "address"
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 2,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 3,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 4,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 5,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    }
                ]
            }
        };
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //
                case "checkbox":
                    this.clickSelectBox(options);
                    break;
                //
                case "selectAll":
                    this.clickSelectedAll(options);
                    break;
            }
        },
        clickSelectBox(options) {
            this.$message("");
        },
        clickSelectedAll(options) {
            this.$message("");
        }
    }
};
</script>
`;

