let customCom = `
//
<template>
    <div>
        <a
            v-if="hasEdit"
            class="table-action"
            v-tooltip="editText"
            @click.stop.prevent="handler('edit', field)"
            
        >{{editText}}</a>
        <a
            v-if="hasDel"
            class="table-action icon-delete"
            v-tooltip="cancelText"
            @click.stop.prevent="handler('delete', field)"
        >{{cancelText}}</a>
        <a
            v-if="hasView"
            class="table-action icon-view"
            v-tooltip="viewText"
            @click.stop.prevent="handler('view', field)"
        ></a>
        <a v-if="!hasEdit && !hasDel && !hasView">--</a>
    </div>
</template>
<script>
export default {
    name: "table-operation",
    props: ["rowData", "field", "index", "tableData", "originData", "action", "keyword"],
    data() {
        return {
            editText: "",
            cancelText: "",
            viewText: ""
        };
    },
    computed: {
        hasEdit() {
            return this.action.indexOf("edit") != -1 && this.rowData.hasEdit !== false;
        },
        hasDel() {
            return this.action.indexOf("delete") != -1 && this.rowData.hasDel !== false;
        },
        hasView() {
            return this.action.indexOf("view") != -1 && this.rowData.hasView !== false;
        }
    },
    methods: {
        handler(type) {
            // 
            let params = {
                type: type,
                index: this.index,
                rowData: this.originData
            };
            this.$emit("on-custom-comp", params);
        }
    }
};
</script>

//
<template>
    <v-table ref="table" :tableOptions="tableData" @on-custom-comp="customCompFunc"></v-table>
</template>

<script>
export default {
    data() {
        return {
            tableData: {
                key: "index",
                css: "table-group",
                columns: [
                    {
                        title: "",
                        field: "index"
                    },
                    {
                        title: "",
                        field: "name"
                    },
                    {
                        title: "",
                        field: "sex",
                        format(data) {
                            return data == 1 ? "" : "";
                        }
                    },
                    {
                        title: "",
                        field: "address"
                    },
                    {
                        title: "action",
                        action: ["edit", "delete"],
                        componentName: 'table-operation'
                    }
                ],
                originData: [
                    {
                        index: 1,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 2,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 3,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    },
                    {
                        index: 4,
                        name: "",
                        sex: "0",
                        address: "xxx"
                    },
                    {
                        index: 5,
                        name: "",
                        sex: "1",
                        address: "xxx"
                    }
                ]
            }
        };
    },
    mounted() {
        //this.tableData.originData = ;
    },
    methods: {
        customCompFunc(options) {
            switch (options.type) {
                //
                case "edit":
                    this.editRule(options);
                    break;
                //
                case "delete":
                    this.delRule(options);
                    break;
            }
        },
        editRule(options) {
            this.$message("");
        },
        delRule(options) {
            this.$message("");
        }
    }
};
</script>
`;

var state = {
    table1: table1,
    customTable: customTable,
    pageTable: pageTable,
    customComTable: customCom
};
var getters = {};

export default {
    namespaced: true,
    state,
    getters
}